.productdisplay{
    display: flex;
    margin: 0px 20px;
}

.productdisplay-left{
    display: flex;
    gap: 17px;
}

.productdisplay-img-list{
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.productdisplay-img-list img{
    height: 163px;
}

.productdisplay-main-img{
    width: 586px;
    height: 700px;
}

.productdisplay-right{
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin: 0px 70px;
}

.productdisplay-right h1{
    color: #3d3d3d;
    font-size: 30px;
    font-weight: 700;
}
.productdisplay-right-star{
    display: flex;
    align-items: center;
    margin-top: 13px; 
    gap: 5px;
    color: #1c1c1c;
    font-size: 16px;
}
.productdisplay-right-prices{
    display: flex;
    margin: 10px 0px;
    font-weight: 700;
    font-size: 24px;
    gap: 20px;
}
.productdisplay-right-price-old{
     color: #818181;
     text-decoration: line-through;
}
.productdisplay-right-price-new{
     color: #ff4141;
}

.productdisplay-right-size h1{
    margin-top: 35px;
    color: #656565;
    font-size: 20px;
    font-weight: 700;
}

.productdisplay-right-size-list{
    display: flex;
    gap: 10px;
    margin: 20px 0px;
}

.productdisplay-right-size-list div{
    padding: 18px 24px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
    cursor: pointer;
    border-radius: 3px;
}
.productdisplay-right button{
    padding: 20px 40px;
    width: 200px;
    font-size: 16px;
    font-weight: 700;
    color: white;
    margin-bottom: 40px;
    border: none;
    outline: none;
    cursor: pointer;
    background: #ff4141;
    border-radius: 10px;

}

